import SearchHeader from '@/components/layouts/SearchHeader.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import confirmLogin from '@/components/confirmLogin.vue';
export default {
	components: {
		SearchHeader,
		uniNavBar,
		confirmLogin
	},
	data() {
		return {
			showconfirmLogin: true,
			goodsList: [{
					goods_id: 0,
					img: '/static/logo.png',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 1,
					img: '/static/logo.png',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 2,
					img: '/static/logo.png',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 3,
					img: '/static/logo.png',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 4,
					img: '/static/logo.png',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 5,
					img: '/static/logo.png',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 6,
					img: '/static/logo.png',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 7,
					img: '/static/logo.png',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				}
			],
			subCategory: [{
				name: '夹克',
				thumb: '/static/logo.png',
			}],
			currentSwiper: 0,
			// 轮播图片
			swiperList: [{
					id: 1,
					src: 'url1',
					img: '/static/logo.png',
				},
				{
					id: 2,
					src: 'url2',
					img: '/static/logo.png',
				},
				{
					id: 3,
					src: 'url3',
					img: '/static/logo.png',
				}
			],
			banners: [],
			category: [],
			sigleBanner: {},
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			scrollLeft: 0 ,//tab标题的滚动条位置
			productList: [],
			hasMore: 1,
		}
	},
	onLoad() {
		this.getCategory()
		this.getBanner()
		this.getList(0)
	},
	methods: {
		// 商品列表
		getList(category) {
			uni.request({
				url: getApp().globalData.api + 'product/index',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					category_id: category
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.productList = res.data.data.list
					this.hasMore = res.data.data.has_more
				}
			});
		},
		// 顶部分类
		getCategory() {
			uni.request({
				url: getApp().globalData.api + 'category/index',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id')
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.category = res.data.data
				}
			});
		},
		// 获取banner图片
		getBanner() {
			uni.request({
				url: getApp().globalData.api + 'system/banners',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id')
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.banners = res.data.data.list
					this.sigleBanner = res.data.data.single
				}
			});
		},
		toProduct(id) {
			uni.navigateTo({
				url: "/pages/product/product_info?id=" + id
			})
		},
		// 滚动切换标签样式
		switchTab: function(e) {
			let that = this;
			this.currentTab = e.detail.current;
			this.checkCor();
		},
		// 点击标题切换当前页时改变样式
		swichNav: function(e) {
			let cur = e.currentTarget.dataset.current;
			if (this.currentTab == cur) {
				return false;
			} else {
				this.currentTab = cur
			}
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor: function() {
			if (this.currentTab > 3) {
				//这里距离按实际计算
				this.scrollLeft = 300
			} else {
				this.scrollLeft = 0
			}
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		}
	}
}
